/**
    @module
*/
import EL from '../EL.js';
/**
    A horizontal ruler
*/
export default class HR extends EL {
	/**
	    Constructs a generic Anchor
	    @param {EL} node The object to contain this element
	    @param {MODEL} model The object model
	 */
	constructor(node, model) {
		super(node, 'HR', model);
	}
}